"use client"

import './globals.css'
import { MetaMaskButton } from "../components/MetamaskButton";
import { useBridge } from "../hooks/useBridge";


export default function Home() {
  // "Lấy" handleBridge ra từ hook
  const { handleBridge, loading } = useBridge();

  const onButtonClick = () => {
    // Truyền các tham số cần thiết vào (ví dụ gateway address và fee token address)
    const gateway = "0xFcDa26cA021d5535C3059547390E6cCd8De7acA6"; 
    const feeToken = "0x1794aB22388303ce9Cb798bE966eeEBeFe59C3a3";
    
    handleBridge(gateway, feeToken);
  };

  return (
    <main className="min-h-screen bg-[#e0f7fa] flex flex-col items-center p-6 md:p-12 font-sans text-black">
      
      {/* Header: Logo và Nút Connect */}
      <div className="w-full max-w-2xl flex justify-between items-center mb-10">
        <h1 className="text-2xl md:text-3xl font-black tracking-tighter uppercase italic">
          OPEN GUILD <span className="text-gray-500 font-bold">BRIDGE</span>
        </h1>
        <MetaMaskButton />
      </div>

      {/* Bridge Card - Neo-Brutalism Style */}
      <div className="bg-white border-[3px] border-black rounded-[32px] shadow-[8px_8px_0px_0px_rgba(0,0,0,1)] p-6 md:p-8 w-full max-w-xl">
        
        {/* FROM - TO Section */}
        <div className="flex flex-col md:flex-row justify-between items-center gap-4 mb-8">
          {/* Network From */}
          <div className="w-full border-2 border-black rounded-2xl p-4 bg-white relative">
            <p className="text-[10px] font-black text-black uppercase mb-1">From</p>
            <div className="font-extrabold flex justify-between items-center">
              <div className="flex items-center gap-2">
                <div className="w-6 h-6 bg-yellow-400 rounded-full border border-black flex items-center justify-center text-[10px]">B</div>
                BNB Chain Testnet
              </div>
              <span>▼</span>
            </div>
          </div>
          
          {/* Switch Button */}
          <button className="bg-yellow-400 border-2 border-black rounded-full p-2 hover:rotate-180 transition-transform shadow-[2px_2px_0px_0px_rgba(0,0,0,1)] z-10">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"><path d="M16 3l4 4-4 4M8 21l-4-4 4-4M20 7H4M4 17h16"/></svg>
          </button>

          {/* Network To */}
          <div className="w-full border-2 border-black rounded-2xl p-4 bg-white">
            <p className="text-[10px] font-black text-black uppercase mb-1">To</p>
            <div className="font-extrabold flex justify-between items-center">
               <div className="flex items-center gap-2">
                <div className="w-6 h-6 bg-blue-100 rounded-full border border-black flex items-center justify-center text-[10px]">E</div>
                Ethereum Sepolia
              </div>
              <span>▼</span>
            </div>
          </div>
        </div>

        {/* AMOUNT Section */}
        <div className="border-2 border-black rounded-2xl p-4 mb-8 bg-white">
          <p className="text-[10px] font-black text-black uppercase mb-2">Amount</p>
          <div className="flex items-center justify-between gap-4">
            <div className="flex items-center gap-2 border-2 border-black rounded-xl px-3 py-2 bg-gray-50 min-w-[120px]">
               <div className="w-5 h-5 bg-blue-500 rounded-full border border-black text-[8px] text-white flex items-center justify-center font-bold">$</div>
               <span className="font-black text-sm">USDC</span> 
               <span className="ml-auto">▼</span>
            </div>
            <input 
              type="number" 
              placeholder="0.00" 
              className="text-3xl font-black text-right outline-none w-full bg-transparent placeholder-gray-300"
            />
          </div>
          <div className="flex justify-between mt-4 items-center">
            <span className="text-xs font-bold text-black">Balance: 6.94</span>
            <div className="flex gap-1">
              {['25%', '50%', '75%', 'Max'].map(p => (
                <button key={p} className="text-[10px] font-bold border-2 border-black rounded-full px-3 py-1 bg-gray-100 hover:bg-yellow-200 transition-colors uppercase">{p}</button>
              ))}
            </div>
          </div>
        </div>

        {/* PROGRESS Bar */}
        <div className="mb-8">
          <div className="flex justify-between text-[10px] font-black uppercase mb-2">
            <span>Transaction Progress</span>
            <span>0%</span>
          </div>
          <div className="w-full h-5 bg-gray-100 border-2 border-black rounded-full overflow-hidden p-0.5">
            <div className="w-[0%] h-full bg-emerald-400 rounded-full border-r border-black"></div>
          </div>
        </div>

        {/* ACTION Button */}
        <button className="w-full bg-[#fff176] border-[3px] border-black py-4 rounded-2xl 
        font-black text-xl shadow-[4px_4px_0px_0px_rgba(0,0,0,1)] 
        hover:translate-y-1 hover:shadow-none transition-all uppercase tracking-widest"
        onClick={onButtonClick} 
        disabled={loading}
        >
          Bridge Now

        </button>
      </div>
    </main>
  );
}